import networklib from './networklib'

export default {

    /**
     * 
     * @param callback
     */
  queryList(callback) {
    networklib.api_get('/cryptocurrencies').then(obj => {
      callback(null, obj.rows);
    })
            .catch(err => { callback(err) });
  },
    /**
     * 
     * @param id
     * @param callback
     */
  queryProduct(id, callback) {
    networklib.get('/productgroups/' + id).then(obj => {
      callback(null, obj);
    })
            .catch(err => { callback(err) });
  }

}

